

import React, {useState} from 'react'

function NftCardComponent(props) {

    const [ dropdownShowing, setDropdownShowing ] = useState(false);

    const cardEvt = evt => {
        setDropdownShowing(!dropdownShowing);
    }

    return (
        <div className="cards">
            <div className={"card" + (dropdownShowing ? " show" : "")} onClick={cardEvt}>
                <div className="card__image-holder" style={{
                        backgroundImage: "url(" + props.src +")",
                        backgroundPosition: "center",
                        backgroundRepeat: "no-repeat",
                        backgroundSize: "cover"
                        }}  alt="wave">
                </div>
                <div className="card-title">
                    <a className="toggle-info btn">
                        <span className="left"></span>
                        <span className="right"></span>
                    </a>
                    <h2>
                        {props.title}
                        <small>Painting from Sparky Lebold</small>
                    </h2>
                </div>
                <div className="card-flap flap1">
                    <div className="card-description">
                        {props.description}
                    </div>
                    <div className="card-offerLink">
                        <a className="btn" target="_blank" href={props.Href}>Make Offer</a>
                    </div>
                </div>
            </div>

        </div>
    );
}

export default NftCardComponent;
